<template>
  <el-row :gutter="20">
    <el-col :span="16" :offset="4">
      <div class="title">
        <h2>{{ title }}</h2>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="proto" prop="protobufFile">
            <el-upload
              class="upload-demo"
              drag
              multiple
              accept=".proto"
              action="#"
              :auto-upload="false"
              :on-change="handleOnchange"
              :on-remove="handleOnRemove"
              :file-list="fileList"
            >
              <i class="el-icon-upload"></i>
              <div class="el-upload__text">
                proto<em></em>
              </div>
              <!-- <div class="el-upload__tip" slot="tip">
                proto
              </div> -->
            </el-upload>
          </el-form-item>

          <el-form-item
            label=""
            prop="serverName"
            style="margin-top: 50px;"
          >
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.serverName"
            ></el-input>
          </el-form-item>
          <el-form-item label="module" prop="moduleName">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.moduleName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i
                class="el-icon-question"
                style="font-size: 20px; vertical-align: middle; color: grey;"
              ></i>
              <div slot="content">
                go.modmodule
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="" prop="projectName">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.projectName"
            ></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.repoAddr"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i
                class="el-icon-question"
                style="font-size: 20px; vertical-align: middle; color: grey;"
              ></i>
              <div slot="content">
                image-repo-hostdockerk8sdockerdocker
              </div>
            </el-tooltip>
          </el-form-item>
          <div style="margin: 30px auto;"></div>
        </el-form>

        <div class="conform-btns">
          <el-button size="default" @click="clearFormData"></el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            ></el-button
          >
          <DownloadCode ref="downloadCode"></DownloadCode>
        </div>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import DownloadCode from "@/components/DownloadCode";
export default {
  name: "MicroRPCPb",
  data() {
    return {
      title: "webrpc gateway",
      path: "micro-rpc-gw-pb",
      helpInfo: `
      <p>protorpc gatewayrpchttphttprpcrpcprotoprotoc</p>
      <br />
      <p>1. </p>
      <p>2. *pb.goserviceswagger<b style="color: green">make proto</b></p>
      <p>3.  <span style="color: chocolate">internal/service/xxx_logic.go</span>,  <span style="color: chocolate">panic("implement me")</span></p>
      <p>4. <b style="color: green">make run</b></p>
      <p>5.  <a href="http://localhost:8080/apis/swagger/index.html" target="_blank">http://localhost:8080/apis/swagger/index.html</a> api</p>
      `,

      labelPosition: "right",
      fileList: [],
      ruleForm: {
        moduleName: "",
        serverName: "",
        projectName: "",
        repoAddr: "",
        protobufFile: "",
      },
      rules: {
        moduleName: [
          { required: true, message: "go module", trigger: "blur" },
        ],
        serverName: [
          { required: true, message: "", trigger: "blur" },
        ],
        projectName: [
          { required: true, message: "", trigger: "blur" },
        ],
        protobufFile: [
          { required: true, message: "proto", trigger: "change" },
        ],
      },
    };
  },
  created: function() {
    const apiUrl = appConfig.spongeServiceAddr + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.moduleName = data.data.moduleName;
          this.ruleForm.serverName = data.data.serverName;
          this.ruleForm.projectName = data.data.projectName;
          this.ruleForm.repoAddr = data.data.repoAddr;
          this.ruleForm.protobufFile = "";
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  components: { DownloadCode },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          // 
          let fd = new FormData();
          // fd.append("param", "param value");
          this.fileList.forEach(item => {
            fd.append("files", item.raw);
          });
          const uploadURL = appConfig.spongeServiceAddr + "/uploadFiles";
          axios
            .post(uploadURL, fd)
            .then(res => {
              if (res.data.code == 0) {
                // 
                const apiUrl = appConfig.spongeServiceAddr + "/generate";
                const arg = `micro rpc-gw-pb --module-name=${this.ruleForm.moduleName} --server-name=${this.ruleForm.serverName} --project-name=${this.ruleForm.projectName} --repo-addr=${this.ruleForm.repoAddr} --protobuf-file=${res.data.data}`;
                this.$refs.downloadCode.execution(
                  apiUrl,
                  this.path,
                  arg,
                  this.helpInfo,
                );
              } else {
                this.$message.error(res.data.msg);
              }
            })
            .catch(err => {
              this.$message.error("");
            });
        } else {
          return false;
        }
      });
    },
    clearFormData() {
      this.ruleForm = { dsnAddr: this.ruleForm.dsnAddr };
    },
    handleOnchange(file, fileList) {
      this.ruleForm.protobufFile = file.name;
      this.fileList = fileList;

      if (fileList.length !== 0) {
        this.$refs.ruleForm.validateField("protobufFile");
      }
    },
    handleOnRemove(file, fileList) {
      if (fileList.length === 0) {
        this.ruleForm.protobufFile = "";
        this.$refs.ruleForm.validateField("protobufFile");
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 50px !important;
}

.form-style {
  border: 2px solid #ddd;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 10px;
  padding: 40px 40px;
}

.form-style /deep/ .el-form-item__label {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  color: #111;
}

.upload-demo /deep/ .el-upload-dragger {
  height: 60%;
  width: 150%;
}

.el-upload-dragger .el-icon-upload {
  margin-top: 20px;
}
.el-upload__text {
  margin-bottom: 20px;
}
</style>



